<template>
  <div id="person-info">
    <a-tooltip style="margin-right: 1.5rem" placement="bottom">
      <template slot="title">
        <span>{{isFullScreen?'':''}}</span>
      </template>
      <span @click="handleFullScreen()">
        <svg-icon style="font-size:1.2rem" icon-class="fullScreen"></svg-icon>
      </span>
    </a-tooltip>
    <!--  -->
    <a-dropdown class="dropdown-menu">
      <a class="ant-dropdown-link" style="color: rgba(10, 10, 10, 0.616)" @click="e => e.preventDefault()">
        {{$store.getters.name}}<a-icon style="margin-left:0.2rem" type="down" />
      </a>
      <a-menu slot="overlay" @click="onClick">
        <a-menu-item key="1">
          <a-icon type="retweet" />
        </a-menu-item>
        <a-menu-item key="2">
          <a-icon type="user" />
        </a-menu-item>
        <a-menu-item key="3">
          <a-icon type="poweroff" />
        </a-menu-item>
      </a-menu>
    </a-dropdown>
    <!--  -->
    <a-avatar :size="48" :src="$store.getters.avatar" />
  </div>
</template>

<script>

export default {
  data () {
    // 
    return {
      isFullScreen: false,
      userInfo: {
      }
    }
  },
  //  data
  computed: {},
  // data
  watch: {},
  //  - this
  created () {
  },
  //  - DOM
  mounted () {

  },
  methods: {
    onClick ({ key }) {
      // console.log(typeof key)
      if (key === '3') {
        this.$store.dispatch('user/logout').then(() => {
          this.$router.replace('/login')
        })
      }
    },
    // 
    handleFullScreen () {
      this.isFullScreen = !this.isFullScreen
      const element = document.documentElement
      if (this.fullscreen) {
        if (document.exitFullscreen) {
          document.exitFullscreen()
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen()
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen()
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen()
        }
      } else {
        if (element.requestFullscreen) {
          element.requestFullscreen()
        } else if (element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen()
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen()
        } else if (element.msRequestFullscreen) {
          // IE11
          element.msRequestFullscreen()
        }
      }
      this.fullscreen = !this.fullscreen
    }
  }
// beforeCreate() {}, // - 
// beforeMount() {}, // - 
// beforeUpdate() {}, // - 
// updated() {}, // - 
// beforeDestroy() {}, // - 
// destroyed() {}, // - 
// activated() {}, //keep-alive
}
</script>
<style lang="scss" scoped>
#person-info{
  display: flex;
  align-items: center;
  .dropdown-menu{
    margin-right: 0.8rem;
    .ant-dropdown-link{
      color: rgba(10, 10, 10, 0.616);
    }
  }
}

</style>
