<!--
    
    @params: width ,100%
    @params: height 350px
    @params: autoResize ,true
    @params: chartOption 
-->
<template>
  <div ref="chart" :style="{ height: height, width: width }" />
</template>
<script>
//  echarts  echarts 
import * as echarts from 'echarts/core'
import ElementResize from 'element-resize-detector'
// import resize from './mixins/resize'
//  Chart
import { BarChart, LineChart, PieChart } from 'echarts/charts'
//  Component
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  LegendComponent,
  ToolboxComponent
} from 'echarts/components'
//  Canvas  CanvasRenderer  SVGRenderer 
import { CanvasRenderer } from 'echarts/renderers'

// 
echarts.use([
  TitleComponent,
  TooltipComponent,
  GridComponent,
  LegendComponent,
  ToolboxComponent,
  BarChart,
  LineChart,
  PieChart,
  CanvasRenderer
])

export default {
  // mixins: [resize],
  name: 'ChartView',
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '350px'
    },
    chartOption: {
      type: Object,
      required: true
    },
    theme: {
      type: String,
      default: 'light'
    }
  },
  data () {
    return {
      // Echarts
      chart: null,
      ElementResizeInstance: null
    }
  },
  watch: {
    // 
    chartOption: {
      deep: true, // 
      handler (newVal) {
        this.setOptions(newVal)
      }
    },
    theme: {
      handler (newVal) {
        this.theme = newVal
        this.chart.dispose()
        this.initChart()
        console.log(this.chart)
      }
    }
  },
  mounted () {
    this.initChart()
  },
  beforeDestroy () {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.windowResizeListener)
    /*  */
    this.chart.dispose()
    /* dispose null */
    this.chart = null
  },
  methods: {
    initChart () {
      /* echartsdom()canvas */
      this.chart = echarts.init(this.$refs.chart, this.theme)
      this.chart.setOption(this.chartOption)
      window.addEventListener('resize', this.windowResizeListener)
      this.EleResizeListener()
      // this.chart.on('click', this.handleClick)
    },

    /* chartechart */
    EleResizeListener () {
      this.ElementResizeInstance = ElementResize({
        strategy: 'scroll',
        callOnAdd: true
      })
      this.ElementResizeInstance.listenTo(this.$refs.chart, () => {
        this.$nextTick(() => {
          if (!this.chart) return
          this.chart.resize()
        })
      })
    },
    /* echart */
    windowResizeListener () {
      if (!this.chart) return
      this.chart.resize()
    }
    // handleClick (params) {
    //   this.$emit('click', params)
    // },
    // setOptions (option) {
    //   this.clearChart()
    //   if (this.chart) {
    //     this.chart.setOption(option)
    //   }
    // }
    // refresh () {
    //   this.setOptions(this.chartOption)
    // },
    // clearChart () {
    //   this.chart && this.chart.clear()
    // }
  }
}
</script>
